html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

:root {
  --font-body: "Open Sans", Arial, sans-serif;
}

.container {
  max-width: 67.5rem;
  margin: 0 auto;
}

h1,
h2,
h3 {
  line-height: 1.1;
  margin: 0;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}